.advantages {
  padding: 94px 135px 36px;
}

.advantages__list {
  display: flex;
  flex-wrap: wrap;
  list-style: none;
  margin: 0;
  padding: 0;
}

.advantages__item {
  width: 570px;
  background-color: $white;
  border: 1px solid #ebebeb;
  border-left: 3px solid $blue;
  min-height: 299px;
  margin-right: 30px;
  margin-bottom: 30px;
  padding: 39px 43px 43px;

  &:nth-child(2n) {
    margin-right: 0;
  }

  svg {
    fill: $blue;
  }
}

.advantages__item:hover {
  background-color: $blue;
  border-color: transparent;
  color: $white;
  cursor: pointer;

  .advantages__name {
    color: $white;
  }

  svg {
    fill: $white;
  }
}

.advantages__name {
  font-family: "Roboto Condensed", "Arial", sans-serif;
  font-size: 24px;
  line-height: 34px;
  color: $blue;
  margin: 14px 0 0;
}

@media (max-width: $tablet) {
  .advantages {
    padding: 54px 24px 18px;
  }

  .advantages__item {
    width: 345px;
    min-height: 232px;
    margin-bottom: 26px;
    padding: 25px 20px 25px 25px;
  }

  .advantages__name {
    margin: 0;
  }

  .advantages__text {
    line-height: 22px;
    margin: 9px 0 0;
  }
}

@media (max-width: $mobile) {
  .advantages {
    padding: 37px 16px 18px 14px;
  }

  .advantages__name {
    font-size: 20px;
    line-height: 29px;
    margin: 0;
  }

  .advantages__item {
    width: 100%;
    min-height: 232px;
    margin-bottom: 20px;
    margin-right: 0;
    padding: 23px 21px 23px 25px;

    svg {
      margin-bottom: 7px;
    }

    &--suppliers {
      padding-right: 11px;
    }
  }

  .advantages__text {
    line-height: 22px;
    margin-top: 9px;
  }
}
